import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LayoutComponent } from "./layout.component";

const routes: Routes = [
    {
        path: "",
        component: LayoutComponent,
        children: [
            { path: "", redirectTo: "employees", pathMatch: "prefix" },
            {
                path: "employees",
                loadChildren: () =>
                    import("./employee/employee.module").then(
                        (m) => m.EmployeeModule
                    ),
            },
            {
                path: "leaves",
                loadChildren: () =>
                    import("./leaves/leave.module").then((m) => m.LeaveModule),
            },
            {
                path: "salary",
                loadChildren: () =>
                    import("./salary/salary.module").then(
                        (m) => m.SalaryModule
                    ),
            },
            {
                path: "transfer",
                loadChildren: () =>
                    import("./transfer/transfer.module").then(
                        (m) => m.TransferModule
                    ),
            },
            {
                path: "beneficiary",
                loadChildren: () =>
                    import("./beneficiary/beneficiary.module").then(
                        (m) => m.BeneficiaryModule
                    ),
            },
        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class LayoutRoutingModule {}
